import './App.css';
import data from './sample.json'

// var series_name_list =[]

function App() {
  function series_list(){
    document.getElementById('ss').innerHTML =" "

    var series_list = Object.values(data['entries'])    
    series_list.filter(ser);
    }

  function ser(element, index, array){

    if(element['programType'] === "series"){
        // series_name_list.push(element['title'])
      document.getElementById('ss').innerHTML +=  `<li style="display:flex">`+element['title']+`</li>`
    }
    document.getElementById('lis').style.display="block"


  }

  function movies_list(){
    document.getElementById('ss').innerHTML =" "

    var series_list = Object.values(data['entries'])    
    series_list.filter(mov);
  }

  function mov(element, index, array){
    if(element['programType'] === "movie"){
      // series_name_list.push(element['title'])
      // console.log(element['title'])
    document.getElementById('ss').innerHTML +=  `<li style="display:flex">`+element['title']+`</li>`
  }
  document.getElementById('lis').style.display="block"

  }

  function model_close(){
    document.getElementById('lis').style.display="none"
    
  }

  return (
    
    <div className="App" >
      <img src="bg.png" alt="" className="image"></img>
      <div className="bg-text">
        <h2>Here We Have !</h2>
        <div id="s" className="Seires">
        <img src="wi2.jpg" alt="" height="100px"width="100px" className="icon" ></img>
      
        <button onClick={series_list}> SERIES </button>


        </div>
        <div id="m" className="movies">
        <img src="wi2.jpg" alt="" height="100px"width="100px" className="icon" ></img>
        <button onClick={movies_list}> MOVIES </button>


        </div>

        <div id="lis"  style={{display:"none"}} className="bg-text1">
          <span className="close"  onClick={model_close}>X</span>
          <div id="ss"> 
           
          </div>
        </div>
      </div>



    </div>
    
  );

}


export default App;
